import React from 'react';
import './DataTable.scss';
import Checkbox from './Checkbox/Checkbox';
import ColumnHeader from './ColumnHeader/ColumnHeader';


export default function DataTable(props) {
  const [sort, setSort] = React.useState(false);
  return (
    <React.Fragment>
      <div className="data-table__spacer" />
      <table className="data-table">
        <thead>
          <tr>
            <th style={{ width: '2.7%' }}><Checkbox checked="checked" /></th>
            <th><ColumnHeader title="Date" sortable={true} /></th>
            <th><ColumnHeader title="Payee" sortable={true} /></th>
            <th><ColumnHeader title="Description" sortable={true} /></th>
            <th><ColumnHeader title="Type" sortable={true} /></th>
            <th><ColumnHeader title="Selection" sortable={true} /></th>
            <th><ColumnHeader title="Reference" sortable={true} /></th>
            <th><ColumnHeader title="VAT" sortable={true} /></th>
            <th><ColumnHeader title="Spent" sortable={true} alignment="right" /></th>
            <th><ColumnHeader title="Received" sortable={true} alignment="right"/></th>
            <th><ColumnHeader title="REC?" sortable={true} /></th>
            <th><ColumnHeader title="Action" sortable={true} alignment="center"/></th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td>Checkbox</td>
            <td>date</td>
            <td>Payee</td>
            <td>Description</td>
            <td>Type</td>
            <td>Selection</td>
            <td>Reference</td>
            <td>VAT</td>
            <td>Spent</td>
            <td>Received</td>
            <td>REC?</td>
            <td>Action</td>
          </tr>
        </tbody>
      </table>
    </React.Fragment>
  );
}
